  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
.before-1 {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  border-bottom: 1px solid #E6E6E6;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.after-1 {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid #E6E6E6;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
i {
  font-style: normal !important;
}
.red {
  color: #F3575B !important;
}
.green {
  color: #64C67A !important;
}
/*app统一变量设定---start*/
@content Bg：#ffffff;
/*app统一变量设定---end*/
.bordertop-1 {
  position: relative;
}
.bordertop-1:before {
  content: "";
  width: 200%;
  height: 1px;
  background: #ddd;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: scale(0.5);
  -webkit-transform-origin: 0 0;
  transform: scale(0.5);
  transform-origin: 0 0;
}
.borderbot-1 {
  position: relative;
}
.borderbot-1:after {
  content: "";
  width: 200%;
  height: 1px;
  background: #ddd;
  position: absolute;
  bottom: 0;
  left: 0;
  -webkit-transform: scale(0.5);
  -webkit-transform-origin: 0 0;
  transform: scale(0.5);
  transform-origin: 0 0;
}
.borderleft-1 {
  position: relative;
}
.borderleft-1:before {
  content: "";
  width: 1px;
  height: 200%;
  background: #ddd;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: scale(0.5);
  -webkit-transform-origin: 0 0;
  transform: scale(0.5);
  transform-origin: 0 0;
}
.borderright-1 {
  position: relative;
}
.borderright-1:after {
  content: "";
  width: 1px;
  height: 200%;
  background: #ddd;
  position: absolute;
  top: 0;
  right: 0;
  -webkit-transform: scale(0.5);
  -webkit-transform-origin: 0 0;
  transform: scale(0.5);
  transform-origin: 0 0;
}
a {
  color: #333;
}
.page {
  background: #f2f2f2;
  width: 100%;
  min-height: 100vh;
  height: auto;
}
.subpage {
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 0.44rem;
  background: #f2f2f2;
  width: 100%;
  min-height: 100vh;
  height: auto;
  box-sizing: border-box;
}
.slide-rightIn-enter-active {
  transition: all 0.3s ease;
}
.slide-rightIn-leave-active {
  transition: all 0.3s ease;
}
.slide-rightIn-enter,
.slide-rightIn-leave-to {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.oneline {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.twoline {
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.divide-20 {
  width: 100%;
  height: 0.2rem;
}
.content {
  background: #FFFFFF;
  width: 100%;
  min-height: 0.5rem;
  height: auto;
  overflow: hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #333;
  margin: 0;
  padding: 0;
}



